"use client"

import { useState } from "react"
import {
    Table,
    TableBody,
    TableCell,
    TableHead,
    TableHeader,
    TableRow,
} from "@/components/ui/table"
import { Card } from "@/components/ui/card"
import { Input } from "@/components/ui/input"
import { Badge } from "@/components/ui/badge"
import { CheckCircle, AlertCircle } from "lucide-react"

const mockTransactions = [
    { id: 1, customer: "Amit Kumar", amount: 250, date: "2024-01-15", status: "success" },
    { id: 2, customer: "Priya Singh", amount: 180, date: "2024-01-14", status: "success" },
    { id: 3, customer: "Vikram Patel", amount: 99, date: "2024-01-13", status: "failed" },
    { id: 4, customer: "Neha Sharma", amount: 500, date: "2024-01-12", status: "success" },
]

export default function TransactionsTable() {
    const [filterDate, setFilterDate] = useState("")

    const transactions = mockTransactions.filter(
        (tx) => !filterDate || tx.date === filterDate
    )

    return (
        <div className="space-y-2">
            {/* Filter */}
            <div className="max-w-xs">
                <Input
                    type="date"
                    value={filterDate}
                    onChange={(e) => setFilterDate(e.target.value)}
                    className="h-9 text-sm"
                    style={{ background: "linear-gradient(to bottom right, #faf5ff, #ffffff)" }}
                />
            </div>

            {/* Table */}
            <Card className="overflow-hidden card">
                <Table>
                    <TableHeader className="bg-muted/40 sticky top-0 z-10">
                        <TableRow>
                            <TableHead className="text-primary">Customer</TableHead>
                            <TableHead className="text-primary">Amount</TableHead>
                            <TableHead className="text-primary">Date</TableHead>
                            <TableHead className="text-primary">Status</TableHead>
                        </TableRow>
                    </TableHeader>

                    <TableBody>
                        {transactions.map((tx) => (
                            <TableRow
                                key={tx.id}
                                className="hover:bg-muted/30 transition-colors"
                            >
                                {/* Customer */}
                                <TableCell className="py-3 font-medium">
                                    {tx.customer}
                                </TableCell>

                                {/* Amount */}
                                <TableCell className="py-3 font-semibold">
                                    â‚¹{tx.amount}
                                </TableCell>

                                {/* Date */}
                                <TableCell className="py-3 text-muted-foreground">
                                    {tx.date}
                                </TableCell>

                                {/* Status */}
                                <TableCell className="py-3">
                                    {tx.status === "success" ? (
                                        <Badge className="gap-1 bg-green-100 text-green-700 hover:bg-green-100">
                                            <CheckCircle size={14} />
                                            Success
                                        </Badge>
                                    ) : (
                                        <Badge className="gap-1 bg-red-100 text-red-700 hover:bg-red-100">
                                            <AlertCircle size={14} />
                                            Failed
                                        </Badge>
                                    )}
                                </TableCell>
                            </TableRow>
                        ))}
                    </TableBody>
                </Table>

                {transactions.length === 0 && (
                    <div className="py-6 text-center text-sm text-muted-foreground">
                        No transactions found
                    </div>
                )}
            </Card>
        </div>
    )
}
